.row {
    margin-left: 0px;
    margin-right: 0px;
}
.dataSourceHeader {
    flex: 0 0 auto;
    /* position: fixed; */
    position: relative;
    top: 0;
    right: 0;
    left: 45px;
    z-index: 1030;
    /* margin-top: 55px; */
    background: #FFFFFF;
    /* box-shadow: 0px 1px 1px #00000029; */
}

.dataSourceHeader .nav-tabs {
    margin-right: -15px;
    margin-left: -110px;
}
 
.dataSourceHeader .nav.nav-tabs {
    justify-content: center;
    border-bottom: 2px solid #dee2e6;
}
.nav-tabs .nav-link:hover,.nav-tabs .nav-link:active,.nav-tabs .nav-link:hover,.nav-tabs .nav-link:visited {
    border-color: #fff;
}
.nav-tabs .nav-link:hover {
    cursor: pointer;
}

.nav-tabs .nav-link.active {
    border-color: #fff #ffff #4796D8;
    border-bottom: 3px solid #4796D8;
}
.navBarRow {
    margin-left: 0px;
    margin-top: 3px;
}
.tabContent {
    display: flex;
    height: calc(100vh - 150px);
    overflow:hiddent;
}
.tabContent .tab-content {
    overflow-y: auto!important;
    /* margin-top: 175px; */
    margin-left: 60px;
    width: 100%;
    /* height: 100vh; */
}
.navBarRow .nav-link,.navBarRow .nav-link:active,.navBarRow .nav-link:focus {
    color: #373A3C;
    /* padding:0rem 1rem 0.25rem; */
    margin-top: 0px;
    padding: 0px 40px 10px;
    font-size: 16px;
    font-weight: bold!important;
}
.navBarRow .nav-link.active {
    color: #000000;
    font-weight: bold!important;;
    padding: 0px 40px 20px;
    margin-top: 0px;
}
.dataSourceHeader nav {
    display: inline-block;
    margin-left: 35px;
}
.dataSourceHeader h2 {
    font-size: 22px!important;
    font-weight: bold!important;
    color: #373A3C;
    display: inline-block;
    margin-top: 20px;
    margin-bottom: -20px;
}
.starIcon {
    margin-top: -5px;
    margin-left: 5px;
    font-size: 20px!important;
    font-weight: 600;
}
@media (max-width: 1760px) {
    .dataSourceHeader .navBarRow {
        margin-top: 5px;
    }
    .dataSourceHeader .nav.nav-tabs {
        margin-left: 0px!important;
    }
    .dataSourceHeader .nav-tabs .nav-link  {
        padding: 0px 20px 20px!important;
    }

}