.tasks {
    padding-left: 35px;
    padding-right: 50px;
}

.tasks th {
    color: #55595C;
    font-size: 16px;
    font-weight: bold;
}

.tasks .search::placeholder {
    font-size: 16px;
    color: #cccccc;
    font-weight: 400;
    padding-left: 20px;
}
.tasks .search {
    width: 239px!important;
    height: 45px;
    background: #FFFFFF;
    border: 1px solid #DDDDDD;
    border-radius: 4px;
    color: #000000;
    font-size: 16px;
    padding-left: 10px;
}
.tasks .search-bar {
    position: relative;
    display: inline-block;
    margin-top: 50px;
}
.tasks .search-icon {
    position: absolute;
    margin-left: 10px;
    z-index: 3000;
    top: 15px;
    color: #ced4da;
}
.tasks .search:focus + .search-icon {
    display: none;
}
.tasks .table {
    margin-top: 10px;
}

.tasks .table .danger{
    color: red;
}