#header {
    background-color: #ffffff!important;
    position: sticky!important;
    padding: 0px!important;
    border-bottom: 1px solid #ced4da !important;
    z-index: 5000;
}
.navbar {
    padding-right: 0px;
    opacity: 1;
}
.searchBar {
    border-radius: 0px!important;
    padding-left: 90px;
    font-size: 30px;
    border: none !important;
    height: 90px;
    margin-left: 20px;
    color: #3F4244;
    display: inline-block;
    color: #000!important;
}
.searchBar:focus{
    border: none;
    box-shadow:none;
}
.searchBar::placeholder {
    color: #ced4da;
    /* text-indent: 75px; */
    font-size: 30px;
    color: #3F4244;
    opacity: 0.5;
    font-style: italic;
}

#header .row {
    width: 100%;
    padding-right: 0px;
}
#header .form-control {
    border: none !important;
    border-radius: 0rem !important;
}