.row {
    margin-left: 0px;
    margin-right: 0px;
}
.syndicate-nav {
    flex: 0 0 auto;
    position: relative;
    margin-top: 20px;
    top: 0;
    right: 0;
    left: 45px;
    z-index: 1030;
}
.syndicate-nav .row {
    margin-left: 15px;
}
.syndicate-nav .navTabRow {
    margin-left: 0px;
    margin-top: 5px;
}
.process-heading {
    margin-left: 45px;
}
.syndicate-nav .nav-tabs {
    margin-right: -15px;
    margin-left: -15px;
}

.syndicate-nav .nav.nav-tabs {
    justify-content: left;
    padding-left: 15rem;
    border-bottom: 2px solid #dee2e6;
}
.nav-tabs .nav-link:hover,.nav-tabs .nav-link:active,.nav-tabs .nav-link:hover,.nav-tabs .nav-link:visited {
    border-color: #fff;
}

.nav-tabs .nav-link:hover {
    cursor: pointer;
}
.nav-tabs .nav-link.active {
    border-color: #fff #ffff #1976d2;
    border-bottom: 3px solid #1976d2;
}
.navTabRow {
    margin-top: -15px;
}

.navTabRow .nav-link {
    color: #000000;
    font-weight: bold;
    font-size: 16px;
}
.syndicate-nav .nav-tabs .nav-item {
    margin-right: 15px;
}
.syndicate-nav .nav-tabs .nav-link:active {
    border-bottom: 1px solid #dee2e6!important;
    margin-top: 2px;
}
.syndicate-nav .nav-link:active {
    border-bottom: 3px solid #1976d2;
}
.process-content {
    display: flex;
    height: calc(100vh - 150px);
}
.process-content .tab-content {
    width: 100%;
    overflow-y: auto;
}
.process-content .tasks {
    margin-left: 75px;
}
.syndicate-container .right-section {
    max-width: 350px;
}
.syndicate-nav .nav-link {
    padding: 0px 40px 20px!important;
}


@media (max-width: 1760px) {
    .syndicate-container .right-section {
        margin-left:-45px!important;
    }
    .syndicate-charts {
        padding-right: 25px;
    }
}