.login-container {
    margin-left: auto;
    display: block;
    margin-right: auto;
    margin-top: 50px;
    border: 1px solid #E5E5E5;
    box-shadow: 0px 10px 30px #DBD8EA80;
    /* box-shadow:  0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23); */
    padding: 2rem 1rem;
    width: 400px;
    height: 315px;
    border-radius: 5px;
}
.login-container .form-control:focus {
    border-color: #ced4da!important;
    box-shadow: none;
}
.login-container .btn.btn-primary:hover,.login-container .btn.btn-primary:focus,.login-container .btn.btn-primary:active {
    box-shadow: none!important;
}