.data-quality .status {
    margin-top: 50px;
    margin-left: 30px;
}
.data-quality .quality-trend {
    margin-top: 25px;
    /* left:20px */
}

.data-quality .updated-date strong {
    margin-top: 50px;
    margin-right: 0px;
    float: right;
    clear: left;
    color: #999999;
    font-size: 16px;
    font-weight: 400;
}
.updated-date .dropdown {
    float: right;
    clear: left;
}
.updated-date .dropdown button.btn-secondary,.updated-date .dropdown button.btn-secondary:hover,.updated-date .dropdown button.btn-secondary:active,.updated-date .dropdown button.btn-secondary:focus {
    background-color: #fff!important;
    border: none!important;
    box-shadow: none!important;
    font-size: 16px;
    color: #1A1818;
    margin-top: 50px;
    margin-right: 60px;
    margin-left: 5px;
    padding: 0px;
    text-transform: none;
}

.data-quality .rule-table {
    margin-top: 15px;
    padding-right: 60px;
    padding-left: 60px;
}
.rule-table thead th {
    font-size: 18px;
    font-weight: bold;
    font-family: Helvetica, Roboto;
    color: #373A3C;
    padding-left: 0px;
}
.rule-table td {
    color: #373A3C;
    font-size: 16px;
    font-family: Helvetica, Roboto;
    padding-left: 0px
}
.rule-table td.status-fail {
    text-transform: uppercase;
    color: #FF0000;
    font-family: Helvetica, Roboto;
    font-weight: 400;
    
}
.rule-table td.status-pass {
    text-transform: uppercase;
    color: #0B9015;
    font-family: Helvetica, Roboto;
    font-weight: 400
}