.list-dataset {
    margin-left: 50px;
    margin-right: 40px;
}
.list-dataset .btn-group {
    margin-top: -5px;
    margin-right: 10px;
}
.list-dataset button.dropdown-toggle::after {
    margin-left: 85px;
    margin-top: 10px;
    font-size: 20px;
}
.list-dataset .btn-group button {
    width: 155px;
    height: 45px;
    background: #FFFFFF!important;
    border: 1px solid #DDDDDD!important;
    border-radius: 4px!important;
    font-size: 16px;
    color: #373A3C;
    text-transform: none;
    box-shadow: none;
    text-align: left;
    padding: 0px 0px 0px 10px;
}
.list-dataset .btn-group button:hover,.list-dataset .btn-group button.btn-secondary:active,.list-dataset .btn-group button.btn-secondary:visited,.list-dataset .btn-group button.btn-secondary:focus {
    background-color: #FFFFFF!important;
    color: #000000;
}
.list-dataset .dropdown {
    display: inline-block;
}
.list-dataset .dropdown button.btn-secondary,.list-dataset .dropdown button.btn-secondary:hover,.list-dataset .dropdown button.btn-secondary:active,.list-dataset .dropdown button.btn-secondary:focus {
    background-color: #fff!important;
    border: none!important;
    box-shadow: none!important;
    font-size: 16px;
    color: #1A1818;
    margin-top: 50px;
    margin-right: 60px;
    margin-left: 5px;
    padding: 0px;
    text-transform: none;
}
.list-dataset th {
    font-size: 18px;
    color: #55595C;
    font-weight: bold;
    padding-bottom: 10px;
}
.list-dataset td {
    font-size: 16px;
    color: #373A3C;
    font-family: Helvetica, sans-serif;
}
.list-dataset tr.parent-row {
    border-left: 4px solid #4796D8;
}
.list-dataset tr.child-row {
    border-left: 2px solid #4796D8;
}
.list-dataset .search::placeholder {
    font-size: 16px;
    color: #cccccc;
    font-weight: 400;
    padding-left: 20px;
}
.list-dataset .search {
    width: 239px!important;
    height: 45px;
    background: #FFFFFF;
    border: 1px solid #DDDDDD;
    border-radius: 4px;
    color: #000000;
    font-size: 16px;
    padding-left: 10px;
}
.list-dataset .search-bar {
    position: relative;
    display: inline-block;
    /* margin-top: 105px!important; */
}
.list-dataset .search-icon {
    position: absolute;
    margin-left: -225px;
    z-index: 2000;
    top: 15px;
    color: #ced4da;
}
.list-dataset .search:focus + .search-icon {
    display: none;
}

.list-dataset th, .list-dataset td {
    border-bottom: 1px solid #DDDDDD!important;
}
.down-arrow,.up-arrow {
    color: #55595C;
}
.list-dataset .parent-row,.list-dataset .child-row {
    background-color: #F7F7F7;
}
strong.version-info {
    color: #999999;
    font-size: 16px;
    font-weight: 400;
    float: right;
}
.dropdown.version-dropdown {
    float: right;
}
.dropdown.version-dropdown .dropdown-toggle {
    margin-top: 0px!important;
    margin-right: 0px!important;
    margin-left: 10px!important;
    font-size: 16px!important;
    color: #373A3C;
    font-weight: 400;
}
 .dropdown.version-dropdown .dropdown-toggle:after {
    margin-left: 10px;
    font-size: 14px;
}
.list-dataset .dropdown-menu button{
    border: none!important;
    height: 35px!important;
}

.list-dataset .table {
    margin-top: 20px;
}

.textStyle {
   color: #0070C9!important;
}

.cellContent {
    width: 200px
}

.desCell {
    width: 350px
}

.tabContent .cellContent a {
    color: #000000;
    text-decoration: none;
}

.list-dataset .table thead th {
    padding: 1.2rem!important;
    padding-bottom: .8rem!important;
    vertical-align: middle!important;
    padding-left: 1.6rem!important;
    border-top: none;
}
.list-dataset .table tbody td {
    padding: 1.2rem!important;
    vertical-align: middle!important;
}
.tabContent .clickableCell a {
    text-decoration: none;
}
.pagination  {
    justify-content: center;
    padding-top: 20px;
    bottom: 10px;
    width: 100%;
}
.pagination li .page-link {
    padding: 1rem 1.25rem;
}