.sampleDataHeader {
    margin-right: 25px;
    margin-top: 2.5rem;
}
.sampleData .sampleDataHeader .btn-primary {
    width: 189px;
    height: 38px;
    background: #4796D8;
    border-radius: 4px;
    font-size: 18px!important;
    box-shadow: none;
    color: #FFFFFF;
    text-transform: none;
    font-weight: normal;
    margin-right: 25px;
}
.sampleDataHeader tbody td {
    color: #55595C;
    font-size: 16px;
    font-weight: normal;
    border-top: 1px solid #DDDDDD;
}
.sampleData .table-responsive {
    padding-right: 25px;
}
.sampleData thead {
    background-color: #F0F0F0;
    border-top: 1px solid #DDDDDD;
    border-bottom: 1px solid #DDDDDD;
}
.sampleData .table th {
    color: #55595C;
    font-size: 18px;
    font-weight: bold;
}
.sampleDataHeader strong {
    font-size: 22px;
    color:#000000;
    font-weight: bold;
}
