.dashboard {
    margin-top: 50px;
    margin-left: 60px;
}

.row {
    margin-left: 0px;
    margin-right: 0px;
}

.dashboard-nav {
    flex: 0 0 auto;
    position: relative;
    top: 0;
    right: 0;
    left: 45px;
    z-index: 1030;
    background: #fff;
    box-shadow: 0px 10px 16px #0000000D;
    /* margin-top: 55px; */
}
.dashboard-nav .row {
    margin-left: 15px;
}
.dashboard-heading {
    margin-top: 20px;
    margin-left: 35px;
}
.dashboard-nav .navBarRow {
    margin-left: 0px;
}
.dashboard-nav .nav-tabs {
    margin-right: -15px;
    margin-left: -15px;
}

.dashboard-nav .nav.nav-tabs {
    justify-content: left;
    padding-left: 12rem;
    border-bottom: 1px solid #dee2e6;
}
.nav-tabs .nav-link:hover,.nav-tabs .nav-link:active,.nav-tabs .nav-link:hover,.nav-tabs .nav-link:visited {
    border-color: #fff;
}
.nav-tabs .nav-link:hover {
    cursor: pointer;
}
.nav-tabs .nav-link.active {
    border-color: #fff #ffff #1976d2;
    border-bottom: 3px solid #1976d2;
}
.navBarRow {
    margin-top: -15px;
}

.navBarRow .nav-link {
    color: #000000;
}
.navBarRow .disable.nav-link {
    color: #ccc !important;
    pointer-events: none;
}
