#impact {
  position: fixed;
  left: 110px;
  width: calc(100vw - 160px);
  height: calc(100vh - 180px);
  overflow: hidden;
}

#impact svg {
  height: inherit;
  width: inherit;
}

path.link {
  fill: none;
  stroke: #c5c5c5;
  stroke-width: 1px;
}

#impact circle {
  fill: #e6e4e4;
  stroke: #fff;
  stroke-width: 1px;
}

#impact rect {
  fill: #e6e4e4;
  stroke: #fff;
  stroke-width: 1px;
}

#impact .impact-dataset {
  fill: #4796d8 !important;
}

text {
  fill: #000000;
  font-size: 12px;
  font-weight: lighter;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: thin !important;
}

.ingoing {
  stroke: #237690 !important;
  letter-spacing: 2px;
  font-weight: 100 !important;
  font-size: 12px;
  /* stroke-width: 3px!important; */
  stroke-width: 1px;
}

.ingoing-text {
  /* stroke: rgb(141, 5, 5) !important; */
  letter-spacing: 2px;
  font-weight: 100 !important;
  font-size: 14px;
  stroke-width: 1px !important;
}

.outgoing {
  stroke: #FA1209 !important;
  letter-spacing: 2px;
  font-size: 12px;
  font-weight: lighter;
}

.outgoing-text {
  color: #FA1209 !important;
  letter-spacing: 2px;
  font-weight: 100 !important;
  font-size: 12px;
}

.selected {
  stroke: #000000 !important;
  letter-spacing: 2px;
  font-weight: lighter;
  font-size: 12px;
}

.selected-text {
  color: #000000;
  letter-spacing: 2px;
  -webkit-text-stroke-width: 0px !important;
}

.float-right {
  float: right;
  margin-right: 10px;
}

.tooltip {
  position: absolute;
  width: 100px;
  height: 50px;
  fill: #FFF !important;
  stroke: rgba(0, 0, 0, .03) !important;
  color: #FA1209 !important;
  opacity: 1 !important;
}

@media (max-width: 1760px) {
  #impact {
    position: relative;
    top: 5px;
    left: -60px;
  }
}