.container {
    margin-left: -22px;
}
.custQuality-container {
    max-width: 100%;
    margin-top: 10px;
    margin-bottom: 50px;
    margin-left: -30px!important;
}
.custQuality-container .row {
    margin-right: -15px;
}
.qualityChart {
    margin-left: 230px;
    width: 150px;
    height: 130px;
}


@media (max-width: 1760px) {
    .container {
        padding-right: 0px;
    }
    .custQuality-container .row {
        margin-right: -25px;
    }
    .dashboard-card {
        margin-left: -5px;
    }
    .data-card,.tabCard {
        width: 135px!important;
        height: 135px!important;
        margin-left: 20px!important;
    }
    .tabCard {
        margin-right: 0px!important;
    }
    .BtabCard,.card-process,.card-syndicate {
        height: 135px!important;
    }
    .tab_heading {
        margin-top: -10px;
        margin-left: -75px;
    }
    .dashboard-card:nth-child(2) {
        margin-left: 0px;
    }
    .BtabCard .card-title {
        margin-top: -4px!important;
    }
}
