.data-dictionary {
    margin-top: 30px;
    margin-left: 30px;
    margin-right: 40px;
}
.data-dictionary .btn-group {
    margin-top: -5px;
    margin-right: 10px;
}
.data-dictionary button.dropdown-toggle::after {
    margin-left: 85px;
    margin-top: 10px;
    font-size: 20px;
}
.data-dictionary .btn-group button {
    width: 155px;
    height: 45px;
    background: #FFFFFF!important;
    border: 1px solid #DDDDDD!important;
    border-radius: 4px!important;
    font-size: 16px;
    color: #373A3C;
    text-transform: none;
    box-shadow: none;
    text-align: left;
    padding: 0px 0px 0px 10px;
}
.data-dictionary .btn-group button:hover,.data-dictionary .btn-group button.btn-secondary:active,.data-dictionary .btn-group button.btn-secondary:visited,.data-dictionary .btn-group button.btn-secondary:focus {
    background-color: #FFFFFF!important;
    color: #000000;
}
.data-dictionary .dropdown {
    display: inline-block;
}
.data-dictionary .dropdown button.btn-secondary,.data-dictionary .dropdown button.btn-secondary:hover,.data-dictionary .dropdown button.btn-secondary:active,.data-dictionary .dropdown button.btn-secondary:focus {
    background-color: #fff!important;
    border: none!important;
    box-shadow: none!important;
    font-size: 16px;
    color: #1A1818;
    margin-top: 50px;
    margin-right: 60px;
    margin-left: 5px;
    padding: 0px;
    text-transform: none;
}
.data-dictionary th {
    font-size: 18px;
    color: #55595C;
    font-weight: bold;
    padding-bottom: 10px;
}
.data-dictionary td {
    font-size: 16px;
    color: #373A3C;
    font-family: Helvetica, sans-serif;
}
.data-dictionary tr.parent-row {
    border-left: 4px solid #4796D8;
}
.data-dictionary tr.child-row {
    border-left: 2px solid #4796D8;
}
.data-dictionary .search::placeholder {
    font-size: 16px;
    color: #cccccc;
    font-weight: 400;
    padding-left: 20px;
}
.data-dictionary .search {
    width: 239px!important;
    height: 45px;
    background: #FFFFFF;
    border: 1px solid #DDDDDD;
    border-radius: 4px;
    color: #000000;
    font-size: 16px;
    padding-left: 10px;
}
.data-dictionary .search-bar {
    position: relative;
    display: inline-block;
    margin-top: 20px!important;
}
.data-dictionary .search-icon {
    position: absolute;
    margin-left: -225px;
    z-index: 2000;
    top: 15px;
    color: #ced4da;
}
.data-dictionary .search:focus + .search-icon {
    display: none;
}

.data-dictionary th, .data-dictionary td {
    border-bottom: 1px solid #DDDDDD!important;
}
.down-arrow,.up-arrow {
    color: #55595C;
}
.data-dictionary .parent-row,.data-dictionary .child-row {
    background-color: #F7F7F7;
}
strong.version-info {
    color: #999999;
    font-size: 16px;
    font-weight: 400;
    float: right;
}
.dropdown.version-dropdown {
    float: right;
}
.dropdown.version-dropdown .dropdown-toggle {
    margin-top: 0px!important;
    margin-right: 0px!important;
    margin-left: 10px!important;
    font-size: 16px!important;
    color: #373A3C;
    font-weight: 400;
}
 .dropdown.version-dropdown .dropdown-toggle:after {
    margin-left: 10px;
    font-size: 14px;
}
.data-dictionary .dropdown-menu button{
    border: none!important;
    height: 35px!important;
}
.data-dictionary tbody td {
    padding: 1.75rem;
    padding-left: 1.1rem;
    vertical-align: middle;
}
.data-dictionary thead th {
    padding: 1rem;
    vertical-align: middle;
    border-top:none;
}