}
.quality-nav {
    flex: 0 0 auto;
    position: fixed;
    top: 165px;
    right: 0;
    left: 15px;
    z-index: 1030;
    background: #fff;
    margin-top: 35px;
}
.qualtiyTab {
    position: unset!important;
    margin-top: 122px;
}
.quality-nav .row {
    margin-left: 15px;
}
.quality-nav .navBarRow {
    margin-left: 0px;
}
.quality-nav .nav-tabs {
    margin-right: -15px;
    margin-left: -15px;
}

.quality-nav .nav.nav-tabs {
    justify-content: left;
    padding-left: 12rem;
    border-bottom: 2px solid #dee2e6;
}
.nav-tabs .nav-link:hover,.nav-tabs .nav-link:active,.nav-tabs .nav-link:hover,.nav-tabs .nav-link:visited {
    border-color: #fff;
}
.nav-tabs .nav-link:hover {
    cursor: pointer;
}
.nav-tabs .nav-link.active {
    border-color: #fff #ffff #1976d2;
    border-bottom: 3px solid #1976d2;
}
.navBarRow {
    margin-top: -15px;
}

.navBarRow .nav-link {
    color: #000000;
}

