.row {
    margin-left: 0px;
    margin-right: 0px;
}
.job-overview-nav {
    flex: 0 0 auto;
    position: relative;
    margin-top: 20px;
    top: 0;
    right: 0;
    left: 45px;
    z-index: 1030;
}
.job-overview-nav .row {
    margin-left: 15px;
}
.job-overview-nav .jobDetailRow {
    margin-left: 0px;
    margin-top: 5px;
}
.job-overview-heading {
    margin-left: 35px;
}
.job-overview-nav .nav-tabs {
    margin-right: -15px;
    margin-left: -15px;
}

.job-overview-nav .nav.nav-tabs {
    justify-content: left;
    padding-left: 15rem;
    border-bottom: 2px solid #dee2e6;
}
.nav-tabs .nav-link:hover,.nav-tabs .nav-link:active,.nav-tabs .nav-link:hover,.nav-tabs .nav-link:visited {
    border-color: #fff;
}
.nav-tabs .nav-link:hover {
    cursor: pointer;
}
.nav-tabs .nav-link.active {
    border-color: #fff #ffff #1976d2;
    border-bottom: 3px solid #1976d2;
}
.jobDetailRow {
    margin-top: -15px;
}

.jobDetailRow .nav-link {
    color: #000000;
    font-weight: bold;
    font-size: 16px;
}
.job-overview-nav .nav-tabs .nav-item {
    margin-right: 15px;
}

.job-overview-nav .nav-link:active {
    border-bottom: 3px solid #1976d2;
}
.job-detail-content {
    display: flex;
    height: calc(100vh - 150px);
}
.job-detail-content .tab-content {
    width: 100%;
    overflow-y: auto;
}
.syndicate-detail-container {
    margin-top: 45px;
    margin-left: 70px;
    padding-bottom: 100px;
}
/* .syndicate-detail-container .volume-imported rect {
    fill: #fff!important;
} */
.syndicate-detail-container .privacy dt {
    white-space: nowrap;
}
.syndicate-detail-container .memory-cpu-usage {
    margin-top: 25px;
}
.syndicate-detail-container .last-run h3,.syndicate-detail-container .next-run h3, .syndicate-detail-container .cadence h3{
    color: #373A3C;
    font-size: 18px!important;
}
.syndicate-detail-container .cadence,.syndicate-detail-container .description {
    margin-top: 10px;
}
.syndicate-detail-container .next-run h3 {
    margin-top: 20px;
}
.syndicate-detail-container .last-run h3 .last-run-date {
    font-size: 16px;
    float: right;
    font-weight: 400!important;
}

.syndicate-detail-container .last-run {
    margin-top: 20px;
}
.syndicate-detail-container .last-run .card {
    background-color: #F0F0F0;
    border-radius: 6px;
    height: auto;
    padding:0.25rem;
    width: auto;
    box-shadow: none;
}
.syndicate-detail-container .last-run dl {
    margin-bottom: 0px;
}
.syndicate-detail-container .last-run dd {
    text-transform: uppercase;
}
.syndicate-detail-container .last-run dt,.syndicate-detail-container .last-run dd {
    color: #000000;
    font-size: 16px;
    text-align: left;
    white-space: nowrap;
    letter-spacing: 0;
    font-weight: normal;
}
.syndicate-detail-container .last-run dd:last-child {
    color: #519626;
    text-transform: capitalize;
    font-weight: 400;
}
.syndicate-detail-container .description p {
    margin-top: -7px;
    
}
.syndicate-detail-container .owner {
    margin-top: 20px;
}
.syndicate-detail-container .overview-domains {
    margin-top: 25px;
}
.line-separator {
    display: inline-block;
}
.syndicate-job-right-section .side-line {
    display: inline-block;
    border-top: 1px solid #707070;
    width: 165px;
    margin-top: 11px;
    margin-left: 0px;
    position: absolute;
}
.syndicate-job-right-section .arrow {
    display: inline-block;
    position: absolute;
    height: 7px;
    width: 7px;
    transform: rotate(135deg);
    transform-origin: center center;
    border-top: 1px solid #707070;
    border-left: 1px solid #707070;
    margin-top: 8px;
    margin-left: 159px;
    margin-bottom: -3px;
}
.grid-circle {
    display: inline-block;
    width: 37px;
    height: 37px;
    background: #FFCF50;
    border-radius: 100px;
}
.grid-icon {
    position: absolute;
    fill: #fff!important;
    margin-left: 6px;
    margin-top:7px;
}
.syndicate-flow .grid {
    display: inline-block;
    float: left;
    margin-top: 10px;
}
.syndicate-flow .grid-text {
    display: block;
    font-size: 11px;
    margin-left: 0px;
}
.syndicate-flow img {
    float: right;
}
/* .syndicate-detail-container rect {
    fill: #fff!important;
} */
.syndicate-job-right-section {
    max-width: 360px;
}

@media (max-width: 1760px) {
    .syndicate-job-right-section .side-line { 
        width: 101px!important;
        margin-left: 0px!important;
    }
    .syndicate-job-right-section .arrow {
        margin-left: 94px!important;
    }
}