.syndicate-container {
    padding-bottom: 75px;
}
.syndicate-charts {
    margin-left: 80px;
    margin-top: 35px;
}
.syndicate-charts .updated-date {
    margin-top: 35px;
    font-size: 14px;
    font-weight: 400;
    color: #000000;
    margin-right: 0px;
}
.syndicate-container .right-section {
    margin-right: 15px;
    /* margin-left: 15px; */
    max-width: 360px;
    /* margin-left: 160px; */
}
.syndicate-container .right-section .summary-table td {
    max-width: 90px;
} 
.syndicate-container .right-section .card {
    width: 145px;
    height: 100px;
    background: #FFFFFF;
    box-shadow: 0px 10px 30px #DBD8EA80;
    border: 1px solid #E5E5E5;
    border-radius: 6px;
}
.syndicate-container .right-section .card .card-body h2 {
    font-size: 36px!important;
    text-align: center;
    font-family: Helvetica;
    color: #575757;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 0px;
}
.syndicate-container .right-section .card-body {
    padding: 1rem 0.5rem;
}

.syndicate-container .right-section .card .card-body p {
    color: #333333;
    font-size: 15px!important;
    text-align: center;
    text-transform: capitalize;
    white-space: nowrap;
    letter-spacing: 0;
}
.syndicate-charts .highcharts-title {
    font-size: 18px!important;
}
.syndicate-charts .cpu-usage rect,.syndicate-charts .memory-usage rect,
.syndicate-charts .record-process rect,.syndicate-charts .volume-process rect {
    fill: #fff!important;
}
.syndicate-charts .memory-cpu-usage {
    margin-top: 25px;
}
