.row {
    margin-left: 0px;
    margin-right: 0px;
}
.connect-nav {
    flex: 0 0 auto;
    position: relative;
    margin-top: 20px;
    top: 0;
    right: 0;
    left: 45px;
    z-index: 1030;
}
.connect-nav .row {
    margin-left: 15px;
}
.connect-nav .navTabRow {
    margin-left: 0px;
    margin-top: 5px;
}
.connect-heading {
    margin-left: 35px;
}
.connect-nav .nav-tabs {
    margin-right: -15px;
    margin-left: -15px;
}

.connect-nav .nav.nav-tabs {
    justify-content: left;
    padding-left: 15rem;
    border-bottom: 2px solid #dee2e6;
}
.nav-tabs .nav-link:active,.nav-tabs .nav-link:visited {
    border-bottom: 3px solid #1976d2;
}
.connect-nav .nav-tabs .nav-link:active {
    cursor: pointer;
    border-bottom: 1px solid #dee2e6!important;
    margin-top: 2px;
}
.connect-nav .nav-tabs .nav-link.active {
    border-color: #fff #ffff #1976d2;
    border-bottom: 3px solid #1976d2!important;
}
.navTabRow {
    margin-top: -15px;
}

.navTabRow .nav-link {
    color: #000000;
    font-weight: bold;
    font-size: 16px;
}
.connect-nav .nav-tabs .nav-item {
    margin-right: 15px;
}

.connect-nav .nav-link:active {
    border-bottom: 3px solid #1976d2;
}
.connect-content {
    display: flex;
    height: calc(100vh - 150px);
}
.connect-content .tab-content {
    width: 100%;
    overflow-y: auto;
}
.connect-container {
    padding-bottom: 75px;
}
.connect-charts {
    margin-left: 70px;
    margin-top: 35px;
}
.volume-chart rect,.volume-chart circle,.record-imported rect,.record-imported circle,
.memory-usage rect,.memory-usage circle,.cpu-usage rect,.cpu-usage circle
{
    fill: #fff!important;
}
.updated-date {
    margin-top: 35px;
    font-size: 14px;
    font-weight: 400;
    color: #000000;
    margin-right: 0px;
}

.connect-container .right-section {
    margin-right: 15px;
    /* margin-left: 15px; */
    width: 360px;
    /* margin-left: 160px; */
}

.connect-container .right-section .card {
    min-width: 145px;
    height: 100px;
    background: #FFFFFF;
    box-shadow: 0px 10px 30px #DBD8EA80;
    border: 1px solid #E5E5E5;
    border-radius: 6px;
}
.connect-container .right-section .card .card-body h2 {
    font-size: 36px!important;
    text-align: center;
    font-family: Helvetica;
    color: #575757;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 0px;
}
.connect-container .right-section .card-body {
    padding: 1rem 0.5rem;
}

.connect-container .right-section .card .card-body p {
    color: #333333;
    font-size: 15px!important;
    text-align: center;
    text-transform: capitalize;
    white-space: nowrap;
    letter-spacing: 0;
}
.summary-table {
    margin-top: 25px;
    border: 1px solid #E5E5E5;
    border-radius: 6px;
}
.summary-table .table {
    width: 100%;
    margin-top: 5px;
    margin-bottom: 0px;
}
.summary-table tr:hover {
    background: none;
}
.summary-table tr:last-child td{
    border-bottom: 1px solid #fff;
}
.summary-table tbody td {
    border: 1px solid #fff;
    border-bottom: 1px solid #E5E5E5;
    padding:1.25rem;
    padding-left: 20px;
    vertical-align: middle;
}
.summary-table .primary-info {
    color: #999999;
    font-size: 14px!important;
    white-space: nowrap;
    padding: 0.5rem;
}
.summary-table .primary-info small {
    display: block;
    font-weight: bold;
    color: #575757;
    text-align: center;
    font-size: 18px;
    text-transform: uppercase;
}
.connect-content .tasks {
    margin-left: 75px;
}
.connect-content .tasks .search-bar {
    margin-top: 50px!important;
}
.connect-content .tasks thead th {
    padding: 1.2rem!important;
    padding-bottom: .8rem!important;
    vertical-align: middle!important;
    padding-left: 1.6rem!important;
    border-top: none;
    font-size: 18px!important;
}
.connect-content .tasks tbody td {
     padding: 1.2rem!important;
     vertical-align: middle!important;
     border-bottom: 1px solid #dee2e6;
}

.connect-container .right-section .summary-table td {
    max-width: 90px;
}
.connect-nav .nav-link.active {
    font-weight: bold!important;
    padding: 0px 40px 20px;
}
.connect-nav .nav-link {
    padding: 0px 40px 20px;
}
@media (max-width: 1760px) {
    .connect-container .right-section {
        margin-left:-45px
    }
    .connect-charts {
        margin-right: 25px;
    }
}