.domains {
    padding-left: 45px!important;
    padding-right: 50px;
}
.domains td, .domains th {
    padding: .80rem;
    vertical-align: middle;
}

.domains th {
    color: #55595C;
    font-size: 16px;
    font-weight: bold;
}

.domains .search::placeholder {
    font-size: 16px;
    color: #cccccc;
    font-weight: 400;
    padding-left: 20px;
}
.domains .search {
    width: 239px!important;
    height: 45px;
    background: #FFFFFF;
    border: 1px solid #DDDDDD;
    border-radius: 4px;
    color: #000000;
    font-size: 16px;
    padding-left: 10px;
}
.domains .search-bar {
    position: relative;
    display: inline-block;
    margin-top: 135px!important;
}
.domains .search-icon {
    position: absolute;
    margin-left: 10px;
    z-index: 3000;
    top: 15px;
    color: #ced4da;
}
.domains .search:focus + .search-icon {
    display: none;
}
.domains .table {
    margin-top: 10px;
}

.progress{
    width: 100px;
    float:right;
    height: 0.5rem;
    margin-top: 0.5rem;
}

.alertIcon {
    width:20px;
    margin-left: -10px;
    margin-right: 7px;
}

.down-arrow,.up-arrow {
    color: #55595C;
}

.domains .table thead th {
    padding: 1.2rem!important;
    padding-bottom: .8rem!important;
    vertical-align: middle!important;
    padding-left: 1.6rem!important;
    border-top: none;
}
.domains .table tbody td {
    padding: 1.2rem!important;
    vertical-align: middle!important;
    border-bottom: 1px solid #dee2e6;
}