.row {
    margin-left: 0px;
    margin-right: 0px;
}
.process-nav {
    flex: 0 0 auto;
    position: relative;
    margin-top: 20px;
    top: 0;
    right: 0;
    left: 45px;
    z-index: 1030;
}
.process-nav .row {
    margin-left: 15px;
}
.process-nav .navTabRow {
    margin-left: 0px;
    margin-top: 5px;
}
.process-heading {
    margin-left: 35px;
}
.process-nav .nav-tabs {
    margin-right: -15px;
    margin-left: -15px;
}

.process-nav .nav.nav-tabs {
    justify-content: left;
    padding-left: 15rem;
    border-bottom: 2px solid #dee2e6;
}
.nav-tabs .nav-link:hover,.nav-tabs .nav-link:active,.nav-tabs .nav-link:hover,.nav-tabs .nav-link:visited {
    border-color: #fff;
}
.process-nav .nav-tabs .nav-link:active {
    cursor: pointer;
    border-bottom: 1px solid #dee2e6!important;
    margin-top: 2px;
}
.nav-tabs .nav-link.active {
    border-color: #fff #ffff #1976d2;
    border-bottom: 3px solid #1976d2;
}
.navTabRow {
    margin-top: -15px;
}

.navTabRow .nav-link {
    color: #000000;
    font-weight: bold;
    font-size: 16px;
}
.process-nav .nav-tabs .nav-item {
    margin-right: 15px;
}
.process-nav .nav-link:active {
    border-bottom: 3px solid #1976d2;
}
.process-content {
    display: flex;
    height: calc(100vh - 150px);
}
.process-content .tab-content {
    width: 100%;
    overflow-y: auto;
}
.process-content .tasks {
    margin-left: 75px;
}

.process-content .jobs .table td ,.process-content .jobs .table th{
    padding: .85rem;
    vertical-align: middle;
}
.process-content .tasks thead th {
    padding: 1.2rem!important;
    padding-bottom: .8rem!important;
    vertical-align: middle!important;
    padding-left: 1.6rem!important;
    border-top: none;
    font-size: 18px!important;
}
.process-content .tasks tbody td {
     padding: 1.2rem!important;
     vertical-align: middle!important;
     border-bottom: 1px solid #dee2e6;
}
.process-nav .nav-link.active {
    font-weight: bold!important;
    padding: 0px 40px 20px!important;
}
.process-nav .nav-link {
    padding: 0px 40px 20px!important;
}
.process-nav .nav-tabs .nav-link.active {
    border-color: #fff #ffff #1976d2;
    border-bottom: 3px solid #1976d2!important;
}
