#sidebar:hover,
nav#sidebar.expanded {
  width: 225px;
  overflow: visible;
  -webkit-transition: width 300ms ease;
  transition: width 300ms ease;
  overflow-y: auto;
  overflow-x: hidden;
}

#sidebar {
  background: #4796D8;
  border-right: 1px solid #e5e5e5;
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  left: 0;
  width: 60px;
  overflow: hidden;
  z-index: 6000;
  -webkit-transition: width 300ms ease;
  transition: width 300ms ease;
}

#sidebar ul li:not(.companyLogo):hover {
  background: #458ad4;
}

#sidebar li.active {
  background: #458ad4;
}

#sidebar ul li.parent-list:hover,
#sidebar ul li.parent-list-2:hover {
  background: none;
}

li.companyLogo {
  background: #000000;
  height: 90px !important;
}

#sidebar li {
  position: relative;
  display: block;
  height: 50px;
}

#sidebar li a:visited {
  background-color: #000000;
}

#sidebar li.parent-list-2 {
  height: 290px;
}

#sidebar li.parent-list {
  height: 190px;
}

#sidebar ul.child-list {
  margin-top: 45px;
}

.child-list a {
  margin-left: 60px !important;
  display: inline-block;
}

#sidebar:hover ul.child-list {
  margin-top: 37px;
}

#sidebar:hover ul.child-list .nav-text,
#sidebar:hover ul.child-list .process-icon+.nav-text,
#sidebar:hover ul.child-list .syndicate-icon+.nav-text {
  top: 18px
}

.pipeline-icon+.nav-text {
  top: 22px !important
}

.child-list,
.child-list a {
  cursor: pointer !important;
}

#sidebar .child-list li {
  height: 50px;
  margin-top: -5px;
}

#sidebar .child-list li .nav-text {
  left: 30px;
}

#sidebar li>a {
  position: relative;
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
  color: #FFFFFF;
  font-family: arial;
  font-size: 14px;
  text-decoration: none;
  margin-left: 22px;
}

#sidebar .nav-icon {
  position: relative;
  display: table-cell;
  width: 60px;
  height: 36px;
  text-align: center;
  vertical-align: middle;
  font-size: 18px;
}

#sidebar .nav-text {
  position: relative;
  display: table-cell;
  vertical-align: middle;
  width: 190px;
  font-size: 12px;
  font-weight: 200;
  text-transform: uppercase;
  color: #FFFFFF;
  letter-spacing: 1px;
  left: 40px;
  top: 17px;
}

#sidebar ul li:not(.companyLogo),
#sidebar li:not(.companyLogo) a {
  cursor: pointer;
}

nav ul,
nav li {
  outline: 0;
  margin: 0;
  padding: 0;
}

.rubik-icon-small {
  position: absolute;
  width: 25px;
  left: -2px;
  height: 14px;
  margin-top: 38px;
}

.rubik-icon-large {
  position: relative;
  display: table-cell;
  vertical-align: middle;
  width: 190px;
  height: 40px;
  left: 40px;
  top: 32px;
}

.notification-icon,
.dashboard-icon,
.birdeye-icon,
.dataset-icon,
.pipeline-icon,
.admin-icon,
.sql-icon,
.connect-icon,
.process-icon,
.syndicate-icon,
.logout-icon,
.documentation-icon,
.app-icon,
.segment-icon,
.customer-icon {
  position: absolute;
  width: 20px;
  height: 20px;
  margin-top: 16px;
}

.pipeline-icon {
  left: 5px;
  height: 30px;
  margin-top: 20px;
}

.connect-icon,
.process-icon,
.syndicate-icon,
.dashboard-icon,
.birdeye-icon,
.customer-icon,
.segment-icon,
.notification-icon
 {
  left: -37px;
  padding-bottom: 10px;
}

.connect-icon,
.process-icon,
.syndicate-icon {
  margin-top: 20px;
}

.logout-icon {
  left: -2px;
}

#sidebar:hover .connect-icon,
#sidebar:hover .process-icon,
#sidebar:hover .syndicate-icon,
#sidebar:hover .dashboard-icon,
#sidebar:hover .birdeye-icon,
#sidebar:hover .customer-icon,
#sidebar:hover .segment-icon,
#sidebar:hover .notification-icon {
  left: 2px !important;
  transition: width 300ms ease;
}
#sidebar:hover .customer-icon {
  top:5px;
}

