.container {
    margin-left: -22px;
}
.customer-container {
    max-width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
    margin-left: -30px!important;
}
.customer-container .row {
    margin-right: -15px;
}
.left-section .container {
    max-width: 100%;
}

.customer-charts {
    margin-left: 10px;
    margin-top: 10px;
}

.customer-container .card {
    display: flex!important;
}

.tabCard,.data-card {
    background-color: white;
    border-color: white;
    margin-left:30px;
    align-items:center;
    border: 1px solid #E5E5E5;
    box-shadow: 0px 10px 30px #DBD8EA80;
}
.data-card {
    width: 180px;
    height: 140px!important;
    flex: none;
}
.customer-container a > .tabCard {
    width: 180px;
    height: 140px;
}
.BtabCard,.card-process,.card-syndicate {
    height: 140px;
}

.customer-container a {
    text-decoration: none;
}
.customer-container .card-title {
    color: #575757;
    font-size: 36px;
    z-index: 1000;
    font-weight: 600;
    height: 40px;
    margin-top: 10px;
    margin-bottom: 20px;
    text-decoration: none;
}

.customer-container .card-text {
    color: #333333;
    font-size: 16px;
    font-weight: 600;
    text-decoration: none;
}


.BtabCard .card-title {
    margin-top: 1px!important;
}

.tab_heading {
    margin-top: -15px;
    margin-left: -145px;
    font-size: 16px;
    font-weight: 400;
    color: #A2A2A2;
}

  .customer-container .BtabCard{
      margin-left: 30px;
      width: 120px;
      align-items:center;
      border-right: 0px;
      box-shadow: 50px 10px 30px 0px #DBD8EA80;
      border-bottom-right-radius: 0px;
      border-top-right-radius: 0px;
      border-right: 0px;
  }

  .card-process {
    width: 120px;
    border-left: 0px;
    border-right: 0px;
    align-items:center;
    box-shadow: 50px 10px 30px 0px #DBD8EA80;
    border-right: 0px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
  }
  .card-syndicate {
      width: 120px;
      border-left: 0px;
      align-items:center;
      box-shadow: 50px 10px 70px 0px #DBD8EA80;
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;
  }

  .subHead {
    margin-left: 44px;
    margin-top: 0px;
    margin-bottom: 30px;
    font-size: larger;
    font-weight: 600;
  }

  .customer-container ul {
    list-style: none;
    line-height: 20px;
    font-size: small;
    padding-inline-start: 0px;
  }

  .feed_heading {
    font-size: medium;
    font-weight: 700;
    margin-bottom: 30px;
    display: inline-block;
  }
  .feeds-link {
      color: #0070C9;
      font-size: 16px;
      text-decoration: none;
      font-style: oblique;
      font-weight: 400;
      font-family: Helvetica;
      
  }

  .customer-container ul li :first-child{
    margin-block-end: 0px;
  }

.select-box {
    float: right;
    display: inline-block;
    width: auto;
    height: 28px;
    color: #373A3C;
    font-size: 14px;
    font-weight: 400!important;
}

.moreButton {
    float: right;
    color: #0070C9;
    font-size: 16px;
    text-decoration: none;
    font-weight: 400;
    font-family: Helvetica;
}

.dashboard-card {
    display: flex;
}



@media (max-width: 1760px) {
    .container {
        padding-right: 0px;
    }
    .customer-container .row {
        margin-right: -25px;
    }
    .dashboard-card {
        margin-left: -5px;
    }
    .data-card,.tabCard {
        width: 135px!important;
        height: 135px!important;
        margin-left: 20px!important;
    }
    .tabCard {
        margin-right: 0px!important;
    }
    .BtabCard,.card-process,.card-syndicate {
        height: 135px!important;
    }
    .tab_heading {
        margin-top: -10px;
        margin-left: -75px;
    }
    .dashboard-card:nth-child(2) {
        margin-left: 0px;
    }
    .BtabCard .card-title {
        margin-top: -4px!important;
    }
}

.tabContent .tab-content {
    overflow-y: auto!important;
    /* margin-top: 85px; */
    margin-left: 60px;
    width: 100%;
    height: 81vh;
}
.progress {
    width: 100%;
    float: none;
    height: 30px;
    margin-top: 0.5rem;
}
