body {
    margin: 0px;
  }

  header {
    height: 55px;
    box-shadow: 0px 0px 5px #cccccc;
    /* position: fixed; */
    z-index: 999999;
    background: #ffffff;
    width: 100%;
    padding: 9px;
    margin-left: 30px;
  }

  #chart {
    position: fixed;
    left: 50px;
    right: 0px;
    top: 155px;
    bottom: 0px;
  }

  path.link {
    fill: none;
    stroke: #c5c5c5;
    stroke-width: 1.0px;
  }

  circle {
    fill: #e6e4e4;
    stroke: #fff;
    stroke-width: 1.5px;
  }

  .circle-source {
    fill: rgb(183, 183, 233);
  }

  .circle-destination {
    fill: rgb(231, 179, 179);
  }

  .circle-node {
    fill: #e6e4e4;
  }

  text {
    fill: #000000;
    font-size: 12px;
    font-weight: lighter;
    font-family: Arial, Helvetica, sans-serif;
  }

  .ingoing {
    stroke: #237690 !important;
    letter-spacing: 2px;
    font-weight: 100 !important;
    font-size: 12px;
  }

  .ingoing-text {
    color: #237690 !important;
    letter-spacing: 2px;
    font-weight: 100 !important;
    font-size: 12px;
    stroke-width: 1px !important;
  }

  .outgoing {
    stroke: #FA1209 !important;
    letter-spacing: 2px;
    font-size: 12px;
    font-weight: lighter;
  }

  .outgoing-text {
    color: #FA1209 !important;
    letter-spacing: 2px;
    font-weight: 100 !important;
    font-size: 12px;
    stroke-width: 1px !important;
  }

  .selected {
    stroke: #000000 !important;
    letter-spacing: 2px;
    font-weight: lighter;
    font-size: 12px;
  }

  .selected-text {
    color: #000000;
    letter-spacing: 2px;
    stroke-width: 1px !important;
  }

  .tooltip {
    position: absolute;
    width: 100px;
    height: 50px;
    fill: #FFF !important;
    stroke: rgba(0, 0, 0, .03) !important;
    color: #FA1209 !important;
    opacity: 1;
  }

  .custom-container {
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
  }

  line {
    stroke: #e6e4e4;
    stroke-width: 2
  }

  .header-left {
    float: left;
    width: 50%;
    height: 50px;
  }

  .header-right {
    float: left;
    width: 50%;
    height: 50px;
  }

  /* .card {
    display: unset;
    border: none;
  } */

  .card select,
  .domain-card select {
    width: 130px;
    height: 35px;
    border-radius: 3px;
    border: 1px solid #cccccc;
  }

  .card strong,
  .domain-card strong {
    width: 130px;
  }

  .form-control-sm {
    padding: 5px;
    font-size: .75rem;
  }

  strong h5 {
    display: inline-block;
  }

  .domain-card {
    display: inline-block;
  }

  .highlightCircle{
    fill:#ffeb3b!important;
    stroke: #3949ab;
  }

  .normalClass{
    fill:#e6e4e4!important;
    stroke: none;
  }

  strong.eyeview-heading {
        margin-left: 30px;
  }